@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  /* background-color: #3f3f3f; */
}


.cert-date {
  display: flex;
  color: white;
  text-shadow: 0.05em 0.05em black;
 }

.cert-date__month {
  font-size: 1rem;
  font-weight: normal;
  margin-right: .5vw;
}

.cert-date__year {
  font-size: 1rem;
  font-weight: normal;
  margin-right: .5vw;
}

.cert-date__day {
  font-size: 1rem;
  font-weight: n;
  margin-right: .5vw;
}

.cert-item-wrapper {
  width: min(700px, 90vw);
  background-color: rgb(240, 237, 237);
  margin-inline: auto;
  margin-top: 2.5vh;
  padding-bottom: 1vh;
  border-radius: 10px;
  border: 1px solid grey;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 5px;
}

.cert-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.1rem;
  padding-inline: 1.5rem;
  margin-bottom: 1vh;
  margin-inline: 2vw;
  background-color: #7f9eb2;
  border: 1px solid black;
  border-radius: 5px;
}

.cert-item h2 {
  width: 40%;
  font-weight: normal;
  font-size: max(0.6rem, 0.6vw);
  font-style: italic;
  -webkit-margin-before: 0.3em;
          margin-block-start: 0.3em;
  -webkit-margin-after: 0.3em;
          margin-block-end: 0.3em;
  border: 1px solid black;
  padding: 0.1rem;
  padding-inline: 1vw;
  background-color: white;
  border-radius: 5px;
}

.cert-item-top-row {
  display: flex;
  justify-content: center;
}

.Cert-item__subjectName {
  width: 80%;
  background-color: transparent;
  color: black;
  padding-top: 1vh;
  margin-left: 2rem;
  margin-bottom: 1vh;
  font-size: 1.2rem;
}

.deleteCert {
  font-size: 1rem;
  font-weight: normal;
  /* border: 1px solid black; */
  border-radius: 50%;
  margin-top: 0.7rem;
  height: 24px;
  width: 24px;
  margin-right: 1rem;
  background-color: #7f9eb2;
  color: rgb(231, 234, 229);
  text-align: center;
}

.deleteCert:hover {
  cursor: pointer;
  background-color: #a3b1ba;
  color: rgb(141, 31, 9);
}

@media (max-width: 1250px) {
  .deleteCert {
    background-color: transparent;
    color: rgb(19, 21, 18);
    font-weight: 700;
  }
}

.certs {
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
}

.infoText1 {
  background-color: white;
  width: min(700px, 90vw);
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 1vh auto 0.3vh;
  border: 2px solid rgb(0, 0, 0);
  border-radius: 10px;
  color: rgb(13, 13, 13);
}

.infoText1 img {
  margin-left: 2vw;

  width: 70%;
  object-fit: cover;
}

.infoText1 h2 {
  width: 60%;
  font-family: "daytona";
  margin-inline: auto;
  font-size: 2rem;
  font-style: italic;
  font-weight: bold;
  -webkit-margin-before: 0.3em;
          margin-block-start: 0.3em;
  -webkit-margin-after: 0.3em;
          margin-block-end: 0.3em;
}

.infoText2 {
  background-color: white;
  width: min(700px, 90vw);
  display: flex;
  margin: 0.3vh auto 0.3vh;
  color: rgb(13, 13, 13);
  justify-content: flex-end;
}

.infoText2 p {
  font-size: 0.75rem;
  -webkit-margin-before: 0.2em;
          margin-block-start: 0.2em;
  -webkit-margin-after: 0.2em;
          margin-block-end: 0.2em;
}

@media (max-width: 650px) {
  .infoText1 img {
    display: none;
  }

  .infoText1 h2 {
    text-align: center;
    width: 60%;
    font-family: "daytona";
    margin-inline: auto;
    font-size: 1.2rem;
  }
}

.certFormToggle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: min(500px, 90vw);
  background-color: rgb(240, 237, 237);
  margin-inline: auto;
  margin-top: 2vh;
  margin-bottom: 3vh;
  padding-top: 1vh;
  padding-bottom: 1vh;
  border-radius: 10px;
  border: 1px solid grey;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 5px;
}

button {
  border: none;
  background-color: transparent;
  line-height: 100%;
  height: 100%;
  width: 100%;
  font-size: 1.1rem;
  color: black;
}

.formLayout {
  display: flex;
  justify-content: center;
  align-items: center;
  width: min(500px, 90vw);
  background-color: rgb(240, 237, 237);
  margin-inline: auto;
  margin-top: 2vh;
  padding-top: 1vh;
  padding-bottom: 1vh;
  border-radius: 10px;
  border: 1px solid grey;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 5px;
  margin-bottom: 3vh;
}

.create-form,
.field {
  width: 100%;
}

.label {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-inline: 10%;
  margin-top: .4vh;
  margin-bottom: .8vh;
  font-size: max(.6rem, .7vw);
  
}

input {
width: 40%;
background-color: #7f9eb2;
color: white;
padding-left: 1vh;
}

input::-webkit-input-placeholder {
color: rgb(212, 206, 199);
padding-left: 1vh;
font-size: max(.6rem, .7vw);
}

input::placeholder {
color: rgb(212, 206, 199);
padding-left: 1vh;
font-size: max(.6rem, .7vw);
}

#buttonsRow {
  margin-top: 2.6vh;
  width: 100%;
display: flex;justify-content: space-around;

}

