.certFormToggle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: min(500px, 90vw);
  background-color: rgb(240, 237, 237);
  margin-inline: auto;
  margin-top: 2vh;
  margin-bottom: 3vh;
  padding-top: 1vh;
  padding-bottom: 1vh;
  border-radius: 10px;
  border: 1px solid grey;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 5px;
}

button {
  border: none;
  background-color: transparent;
  line-height: 100%;
  height: 100%;
  width: 100%;
  font-size: 1.1rem;
  color: black;
}

.formLayout {
  display: flex;
  justify-content: center;
  align-items: center;
  width: min(500px, 90vw);
  background-color: rgb(240, 237, 237);
  margin-inline: auto;
  margin-top: 2vh;
  padding-top: 1vh;
  padding-bottom: 1vh;
  border-radius: 10px;
  border: 1px solid grey;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 5px;
  margin-bottom: 3vh;
}

.create-form,
.field {
  width: 100%;
}

.label {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-inline: 10%;
  margin-top: .4vh;
  margin-bottom: .8vh;
  font-size: max(.6rem, .7vw);
  
}

input {
width: 40%;
background-color: #7f9eb2;
color: white;
padding-left: 1vh;
}

input::placeholder {
color: rgb(212, 206, 199);
padding-left: 1vh;
font-size: max(.6rem, .7vw);
}

#buttonsRow {
  margin-top: 2.6vh;
  width: 100%;
display: flex;justify-content: space-around;

}

